import React from "react";
import { NavLink } from "react-router-dom";

const Navbar = () => {
  return (
    <>
      <nav className="navbar navbar-expand-lg navbar-light bg-light sticky-top">
        <div className="container-fluid">
          <NavLink className="navbar-brand ml-5" to="/">
            <img src="images/s.png" alt="logo" id="Logo_Navbar" />
          </NavLink>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav ml-auto mb-2 mb-lg-0">
              <li className="nav-item ml-10">
                <a
                  className="nav-link "
                  href="https://smartcollege-chat.herokuapp.com/"
                  id="Id_Navbar"
                >
                  chat
                </a>
              </li>

              <li className="nav-item ml-10 ">
                <NavLink
                  activeClassName="active_navbar"
                  className="nav-link underLine"
                  to="/assignments"
                >
                  assignments
                </NavLink>
              </li>

              <li className="nav-item ml-10 ">
                <NavLink
                  activeClassName="active_navbar"
                  className="nav-link underLine"
                  to="/classnotes"
                >
                  classnotes
                </NavLink>
              </li>

              <li className="nav-item ml-10">
                <NavLink
                  activeClassName="active_navbar"
                  className="nav-link underLine"
                  to="/announcements"
                >
                  announcements
                </NavLink>
              </li>

              <li className="nav-item ml-10">
                <NavLink
                  activeClassName="active_navbar"
                  className="nav-link underLine"
                  to="/challenges"
                >
                  challenges
                </NavLink>
              </li>

              <li className="nav-item ml-10">
                <NavLink
                  activeClassName="active_navbar"
                  className="nav-link underLine"
                  to="/internships"
                >
                  internships
                </NavLink>
              </li>
              <li className="nav-item ml-10">
                <NavLink
                  activeClassName="active_navbar"
                  className="nav-link underLine"
                  to="/courses"
                >
                  courses
                </NavLink>
              </li>

              <li className="nav-item ml-10">
                <NavLink
                  className="nav-link w-32 p-2 text-white bg-black hover:opacity-90 text-center rounded-md "
                  to="/contact"
                  id="Contact_Navbar"
                >
                  contact
                </NavLink>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </>
  );
};

export default Navbar;
